h1 {
    color: rgb(91, 91, 242);
}

.containerMain {
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: center;
    align-items: center;
    position: relative;
    top: 50px;
}

.Lab {
    width: 100px;
    height: auto;
    padding: 10px;
    border: solid 1px black;
    border-radius: 10px 10px 10px 10px;
}

.Buttons {
    width: 50%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 70px;
    margin-bottom: 270px;
}